import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

Vue.http.interceptors.push((request, next) => {
  // modify request
  request.headers.set('Auth', 'mxf')

  // //Post传递数据的形式 request payload 与 form data 的模式转变需改变content-type
  // request.headers.set('Content-Type', 'application/x-www-form-urlencoded')

  console.log('请求拦截', request)
  // continue to next interceptor
  next((response) => {
    console.log('拦截服务器响应的数据', response)
    // modify response
    response.headers.set('Authw', 'mxf')
    response.dataMXF = { d: 'mxf 给出的数据' }
  })
})



