<template>
    <template v-if="showNow === 'ajax'">
    <hello></hello>
    <p>从服务器获取的状态：{{passwordStatus}}</p>
    <template v-if="debug">
      <button @click="click">click</button>
    </template>
    </template>
    <slide v-if="showNow === 'imgChange'"></slide>
    <game v-if="showNow === 'game'"></game>
    <!-- <password-input v-if="showNow === 'password'"></password-input> -->
    <group-img v-if="showNow == 'groupImg'"></group-img>
    <myfish v-if="showNow == 'myfish'"></myfish>
</template>

<script>
import Hello from './components/Hello'
import slide from './components/slide'
import './jsCore/api'
import { RPC2 } from './jsCore/core'
import game from './components/game.vue'
// import passwordInput from './components/passwordInput.vue'
import groupImg from './components/groupImg.vue'
import myfish from './components/myfish.vue'

export default {
  components: {
    myfish,
    groupImg,
    // passwordInput,
    game,
    slide,
    Hello
  },
  data: function () {
    return {
      debug: true,
      showNow: 'myfish',
      passwordStatus: '默认值'
    }
  },
  ready () {
    var ss = window.location.href
    var params = ss.split('?')[1]
    if (params) {
      var keyarray = params.split('&')
      var obj = {}
      for(var i = 0; i < keyarray.length; i++) {
        var tmp = keyarray[i].split('=')
        obj[tmp[0]] = tmp[1]
      }
      if (obj.show !== undefined) {
        this.showNow = obj.show
      }
    }

    // console.log('在dom加载完毕后，发起一个请求.post登录命令1')
    // var obj = {
    //   username: 'admin',
    //   password: 'admin'
    // }
    // let that = this
    // this.$http.post('/api/login', obj).then(function (data) {
    //   this.passwordStatus = data.status
    //   console.log('成功？服务器校验的结果', data)
    // }, function (data) {
    //   // 有没有发现这里的this很神奇~
    //   this.passwordStatus = data.status
    //   console.log('ready的login 失败？服务器校验的结果', data)
    //   setTimeout(function () {
    //     console.log('ready的login 失败？读取js请求拦截器修改的数据: 数据信息', data.dataMXF)
    //     console.log('ready的login 失败？读取js请求拦截器修改的数据: header信息', data.headers.get('Authw'))
    //     that.passwordStatus = data.dataMXF.d
    //   }, 5000)
    // })
  },
  methods: {
    click () {
      console.log('click::用户主动触发调用ajax请求')
      let that = this
      let data = {a:2,b:3}
      that.debug = false
      RPC2(data).then(function () {}, function () {
        setTimeout(function () {
          console.log('!')
          that.debug = true
        },5000)
      })
    }
  }
}
</script>

<style>
/**{margin:0;padding: 0;}*/
html {
  height: 100%;
}
body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
</style>



