<template>
  <div class="game">
    
  </div>
</template>

<script>
import '../jscore/hilo-standalone.js'
var me
export default {
  ready () {
    me = this
    this.initGmae()
  },
  data () {
    return {
      stage: {},
      ticker: {},
      bird: {}
    }
  },
  methods: {
    initGmae: function () {
      var clientWidth = document.body.clientWidth;
      var clientHeight = document.body.clientHeight;
      var containerElem = document.body.querySelector('.game')
      var stage = this.stage = new Hilo.Stage({
          renderType:'canvas',
          container: containerElem,
          width: clientWidth,
          height: clientHeight
      });
      this.stage.onUpdate = this.onUpdate
      var ticker = this.ticker = new Hilo.Ticker(60);
      ticker.addTick(stage);
      ticker.start();

      this.bg = new Hilo.Bitmap({
        width:clientWidth,
        height: clientWidth/375*271,
        x: -100,
        y: 0,
        image: '//images.kaistart.com/testBackground161229.png'
      }).addTo(stage)

      this.bird = new Hilo.Bitmap({
        width: 50,
        height: 50,
        x: 20,
        y: 6,
        image: '//images.kaistart.com/header1.png'
      }).addTo(stage);
      // var speedX = 1;
      // var speedY = 1;
      // var dirX = 'right';
      // var dirY = 'down';
      this.bird.dirX = 'right';
      this.bird.dirY = 'down';
      this.bird.speedX = 1;
      this.bird.speedY = 1;
      this.bird.onUpdate = function () {
        if (!this.pause) {
          if (this.y >= (clientHeight-this.height) && this.dirY === 'down') {
            this.speedY = -1
            this.dirY = 'up'
          } else if (this.y < 0 && this.dirY === 'up') {
            this.speedY = 1
            this.dirY = 'down'
          }
          if (this.x >= (clientWidth - this.width) && this.dirX === 'right') {
            this.speedX = -1
            this.dirX = 'left'
          } else if (this.x <= 0 && this.dirX === 'left') {
            this.dirX = 'right'
            this.speedX = 1
          }
          this.y = (this.y + this.speedY * 0.6)
          this.x = (this.x + this.speedX * 0.6)          
        }
        // console.log(this.x, '--', this.y, 'this.dirX', this.dirX, this.dirY)
      }
      stage.enableDOMEvent(Hilo.event.POINTER_START, true);
      stage.enableDOMEvent(Hilo.event.POINTER_END, true);
      stage.on(Hilo.event.POINTER_START, this.touchStart);
      stage.on(Hilo.event.POINTER_END, this.touchEnd);
      this.ticker.addTick(Hilo.Tween);//需要把Tween加到ticker里才能使用 
    },
    onUpdate: function () {
      // console.log(me)
    },
    touchStart: function (e) {
      console.log('触摸？', e.eventTarget, e.stageX, e.stageY);
      console.log(this.bird)
      this.bird.pause = true
      this.bird.y = e.stageY - this.bird.height/2
      this.bird.x = e.stageX - this.bird.width/2
      return false
    },
    touchEnd: function () {
      this.bird.pause = false
      return false
    }
  }
}
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
</style>
<style scoped>

.game{
  width: 100%;
  height: 100%;
  /*border: 1px dotted #ccc;*/
  /*background-color: #ccc;*/
}
</style>



