<template>
<div class="fl">
  <div class="wrap">
    <img draggable="true" class="pr bg" id="idbg">
    <img draggable="true" class="pa" id="idheader">
    <div draggable="true" class="pa" id="idqr"></div>
    <div draggable="true" class="pa text" id="idtext">{{items[3].text}}</div>
  </div>
  <br style="clear:both;">
  <p> <input type="button" value="1、首先合成图片" @click="doit"> <input type="button" value="2、生成img图片,供保存" @click="doImgit"> </p>
  <p>点击按钮1生成的合成图像长按是没反应的。完了再点击按钮2会生成标准的图片，供另存为和转发</p>
</div>
<div class="fr output">
  <canvas v-show="showType === 'canvas'" id="can"></canvas>
  <img v-show="showType === 'img'" id="canimg" >
</div>
</template>
<script>
  (function() {
    var hm = document.createElement("script");
    hm.src = "//static.kaistart.com/qrcode.js";
    var s = document.getElementsByTagName("script")[0]; 
    s.parentNode.insertBefore(hm, s);
  })()
  module.exports = {
    data () {
      return {
        showType: 'canvas',
        items: [{
          name: 'bg',
          img: 'http://images.kaistart.com/o_1bg5nb3lr15021mo06oo1e4s1gi214.png?imageView2/3/w/1260',
          id: 'idbg',
          top: 0,
          left: 0,
          width: 0,
          height: 0
        }, {
          name: 'header',
          img: 'http://images.kaistart.com/header1.png',
          id: 'idheader',
          top: 0,
          left: 0,
          width: 0,
          height: 0
        }, {
          name: 'qr',
          img: 'http://www.weibo.com/u/2217974432',
          id: 'idqr',
          top: 200,
          left: 0,
          width: 120,
          height: 120
        }, {
          name: 'text',
          img: '',
          id: 'idtext',
          text: '我是个人名',
          top: 320,
          left: 0,
          width: 0,
          height: 0
        }],
        qrcode: '',
        canvas: '',
        context: ''
      }
    },
    create () {
    },
    ready () {
      window.mm = this
      console.log('ready!')
      this.canvas = document.getElementById('can')
      this.context = this.canvas.getContext('2d')
      var that = this
      setTimeout(function () {
        that.allInit()
        that.dragEvents()
      }, 400)
    },
    methods: {
      initQr: function (w, h, url) {
        let that = this
        if (typeof QRCode !== 'undefined') {
            that.qrcode = new QRCode("idqr", {
              typeNumber: 2,
              text: url,
              width: w,
              height: h,
              colorDark : "#000000",
              colorLight : "#ffffff",
              correctLevel : 3
            })
        } else {
          setTimeout(function () {
            that.initQr(w, h, url)
          }, 200)
        }
      },
      allInit: function () {
        let that = this
        let arr = that.items
        for(let i = 0; i < arr.length; i++) {
          let data = arr[i]
          if (data.img !== '') {
            if (data.name === 'qr') {
              data.width = 120
              data.height = 120
              that.initQr(data.width, data.height, data.img)
            } else {
              var imgDom = document.getElementById(data.id)
              imgDom.setAttribute('crossOrigin', 'anonymous')
              imgDom.setAttribute('src', data.img)
              imgDom.onload = function () {
                data.width = this.width
                data.height = this.height
                // 初始化最终的画布
                if (data.name === 'bg') {
                  console.log('========> canvas init')
                  that.canvas.setAttribute('width', data.width)
                  that.canvas.setAttribute('height', data.height)
                }
              }
            }
          } else {
            setTimeout(function () {
            var textDom = document.getElementById(data.id)
            data.width = textDom.clientWidth
            data.height = textDom.clientHeight
            console.log('CCCCCCCtext',textDom , textDom.clientWidth, textDom.clientHeight)
            }, 1000)
          }
        }
      },
      dragEvents: function () {
        let arr = this.items
        for(let i = 0; i < arr.length; i++) {
          let domNow = document.getElementById(arr[i].id)
          let dragDomType = arr[i].name

          domNow.addEventListener('drag', function (ev) {
            // ev.target.offsetLeft
            // ev.target.offsetTop
            console.log('drag!->1', dragDomType, ev.target.offsetLeft, '--', ev.target.offsetTop, ev)
            console.log('drag!->2', dragDomType, ev.offsetX, '--', ev.offsetY, ev)
          })
          domNow.addEventListener('dragend', function (ev) {
            console.log('end!->', arr[i], dragDomType, ev)
            let width = arr[i].width
            let height = arr[i].height
            var left, top
            if (dragDomType !== 'text') {
              left = arr[i].left + ev.offsetX - parseInt(width/2)
              top = arr[i].top + ev.offsetY  - parseInt(height/2)
              // left = arr[i].left + ev.offsetX
              // top = arr[i].top + ev.offsetY
            } else {
              left = arr[i].left + ev.offsetX
              top = arr[i].top + ev.offsetY - height
            }
            domNow.style.left =  left + 'px'
            domNow.style.top = top + 'px'
            arr[i].left = left
            arr[i].top = top
            console.log('end!->', i, dragDomType, ev)
          })          
        }
      },
      // 转换成base64编码
      doImgit: function () {
        var ss = this.canvas.toDataURL()
        if (ss.length < 1000) {
          window.alert('先点击第一个按钮啊！！')
        } else {
          var dom = document.getElementById('canimg')
          dom.src = ss
          this.showType = 'img'
        }
      },
      // 开始生成图片
      doit: function () {
        this.showType = 'canvas'
        let arr = this.items
        for(let i = 0; i < arr.length; i++) {
          let data = arr[i]
          let imgDom = document.getElementById(data.id)
          let left = data.left
          let top = data.top
          let width = data.width
          let height = data.height
          if (data.name === 'qr') {
            imgDom = imgDom.querySelector('img')
          }
          if (data.name !== 'text'){
            this.context.drawImage(imgDom, left, top, width, height)
          } else {
            this.context.font = "24px Microsoft YaHei"
            this.context.fillStyle = "white"
            this.context.fillText(data.text, left, top + height)
          }
        }
        // let bg = document.getElementById('idbg')
        // this.context.drawImage(bg, 0, 0, bg.width, bg.height)
        // let header = document.getElementById('idheader')
        // this.context.drawImage(header, , parseInt(header.style.top) || 0, header.width, header.height)
        // let qr = document.getElementById('idqr')
        // let qrimg = document.querySelector('#qrcode img')
        // this.context.drawImage(qrimg, parseInt(qr.style.left) || 0, parseInt(qr.style.top) || 0, 120, 120)
      }
    }
  }
</script>
<style type="text/css">
.wrap{
  position: relative;
  float: left;
  border: 11px solid #ccc;
  margin:10px;
}
.fl{float: left;}
.fr{float: right;}
.pr{position: relative;}
.pa{position: absolute;}
.pa{top: 0;left: 0;}
.bg{
  display: block;
}
#idqr{top: 200px;}
.text{
  color: white;
  font-size: 24px;
  font-weight: 800;
  top: 320px;
}
.output{
  margin: 10px;
}
@media screen and (max-width: 700px) {
  #canimg,
  #can{
    width: 100%;
  }
}
</style>


