<template>
  <canvas id="fishcanvas" class="fishwrap" :width="can.width" :height="can.height"></canvas>
  <!-- <img src="/static/fishbg1.jpeg" alt=""> -->
  <canvas id="fish" class="none" width="100" height="50" style="border: 1px solid #ccc; display:'';"></canvas>
  <div class="func none">
    <a href="javascript:;" class="btn" @click="isay">跟它说个秘密</a>
  </div>
  <div class="modal talk">
    
  </div>
</template>
<script>
module.exports = {
  data () {
    var that = this
    return {
      can: {
        dom: {},
        context: {},
        rate: 1,
        width: 300,
        height: 500
      },
      fps: [],
      fpsshow: 0,
      bg: {
        bg1: {
          dom: {},
          // width: 200,
          left: 200,
          offleft: 0,
          top: 150,
          offtop: 0
        }
      },
      // 用户行为记录。
      useractions: [{
        day: '',
        acts: [{
          time: '',
          act: ''
        }]
      }],
      deviceinfo: {
        useraction: '', // 根据数据计算用户当前的行为。
        wayRotate: 0, // 设备的朝向。东南西北。
        leftOrRight: 0, // 设备的左右倾斜。 左倾斜的程度 [-9，0]。右倾斜的程度 [0,9]。
        fwordorback: 0 // 设备的前后程度。
      },
      fish: {
        dom: {},
        context: {},
        headtoword: 1, // default: 1
        // 初始化我的鱼🐟
        init: function () {
          var imgDom = document.createElement('img')
          imgDom.setAttribute('crossOrigin', 'anonymous')
          imgDom.setAttribute('width', that.fish.size.width)
          imgDom.setAttribute('height', that.fish.size.height)
          imgDom.setAttribute('src', that.fish.looks[0].img)
          // imgDom.setAttribute('style', 'transform: rotate(90deg);background:red;')
          that.fish.looks[0].dom = imgDom
          console.log(that.fish.looks[0].dom)
          imgDom.onload = function () {
            that.fish.paint()
          }
          // that.fish.looks[0].img
        },
        // 把鱼画到布上
        paint: function() {
          if (that.pause) {
            return false
          }
          var bg = that.fish.looks[0]

          that.fish._computedFish()

          var p = that.fish.positon
          that.fish.context.clearRect(0,0,1000,1000)
          var fctx = that.fish.context
          if (that.fish.headtoword == 1){
            fctx.save();
            fctx.translate(0, 0);//将新的坐标原点设置为(0,图片高度) 
            fctx.scale(-1, 1);//垂直向上翻转 
            fctx.drawImage(bg.dom,-100,0,100,60)
            fctx.setTransform(1, 1, 1, 1, 1, 1);
            fctx.restore();//恢复原点和翻转 
          } else {
            fctx.drawImage(bg.dom,0,0,100,50)
          }

          that.changecolor()
        },
        do:function (act, bool, params) {
          that.fish.acts[act] = bool === true
          // console.log('rotate')
          Object.keys(that.fish.acts).map(function (item, index) {
              switch (act) {
                case 'walk':
                  if (that.fish.acts[item] === true) {
                    if (typeof params === 'object') {
                      that.fish.action[item](params.x, params.y, params.z || 1)
                    } else {
                      var x = Math.random()
                      x = x > 0.5 ? x * 0.1 : x
                      that.fish.action[item](0.1 + x,0.1,1)
                    }
                  } else {
                    that.fish.action[item](0,0,0)
                  }
                break;
                case 'talk':
                  if (that.fish.acts[item] === true){
                    that.fish.do('walk',false) // 先停止移动
                    if (params !== undefined) {
                      that.fish.word.text = params
                    } else {
                      that.fish.word.text = '他们说，我只有7秒钟的记忆！'
                    }
                    that.fish.word.isTalking = true
                  } else {
                    that.fish.word.isTalking = false
                    that.fish.do('walk',true)
                  }
                break;
                default:
                break;
              }
          })
        },
        // 重新计算鱼的位置
        _computedFish: function () {
          // if(that.fish.positon.y > 900) {
          //   debugger
          // }
          var f = that.fish
          f.positon.x += f.moveSpeed.x
          f.positon.y += f.moveSpeed.y
          f.positon.z += f.moveSpeed.z
          if ((f.positon.y > 650 && f.moveSpeed.y > 0) || (f.positon.y < 50 && f.moveSpeed.y < 0)) {
            f.moveSpeed.y = f.moveSpeed.y * (-1)
          }
          if ((f.positon.x > 690 && f.moveSpeed.x > 0)  || (f.positon.x < 0 && f.moveSpeed.x < 0)) {
            f.moveSpeed.x = f.moveSpeed.x * (-1)
            f.headtoword = f.moveSpeed.x > 0 ? 1 : -1
          }
          // console.log('鱼的位置', f.moveSpeed.x, f.moveSpeed.y, f.positon.x, f.positon.y)
          // console.log('鱼的速度', f.moveSpeed.x, f.moveSpeed.y)
          // f.context.rotate(180*Math.PI/180)
        },
        fishloop: function () {
          that.fish.action.talk()
        },
        acts: {
          walk: true,
          talk: false
        },
        action: {
          walk: function (x,y,z) {
            var speed = that.fish.moveSpeed
            var dir = that.fish.headtoword > 0 ? 1 : -1
            speed.x = x * dir * 2.1
            speed.y = y * (speed.y >= 0 ? 1 : -1) * 2.1
            speed.z = z * (speed.z >= 0 ? 1 : -1)
            // that.fish.dom.setAttribute('style', 'transform:scale(3.5);')
            // that.fish.context.scale(1.5,1.5)
          },
          talk: function () {
            var ff = that.fish
            var bgcxt = that.can.context
            var width = 350
            var height = 50
            var marginTop = 10 // 对话框上下方向上 偏离鱼头顶的距离。
            if (ff.word.isTalking) {
              bgcxt.beginPath();
              bgcxt.moveTo(ff.positon.x, ff.positon.y - height)
              bgcxt.lineTo(ff.positon.x, ff.positon.y - marginTop)
              bgcxt.lineTo(ff.positon.x + 150, ff.positon.y - marginTop)
              bgcxt.lineTo(ff.positon.x + 150, ff.positon.y)
              bgcxt.lineTo(ff.positon.x + 190, ff.positon.y - marginTop)
              bgcxt.lineTo(ff.positon.x + width, ff.positon.y - marginTop)
              bgcxt.lineTo(ff.positon.x + width, ff.positon.y - height)
              bgcxt.closePath()
              bgcxt.fillStyle="white";
              bgcxt.fill();
              bgcxt.stroke();
              
              bgcxt.font = '14px serif';
              bgcxt.fillStyle = 'black';
              bgcxt.fillText(ff.word.text , ff.positon.x + 5, ff.positon.y - 30);
            }
          },
          hide: function () {}
        },
        looks: [{
          dom: {},
          img: '/mxf_kaistart/dist/static/fish_action_0001.png',
          positon: {
            x: 0,
            y: 0,
            z: 0
          }
        }],
        positon: {
          x: 300,
          y: 600,
          z: 0
        },
        moveSpeed: {
          x: 0,
          y: 0,
          z: 0
        },
        size: {
          width: 100,
          height: 50
        },
        word: {
          isTalking: false,
          time: '2017-6-21',
          text: '他们说，我只有7秒钟的记忆！'
        }
      }
    }
  },
  ready: function () {
    console.log('ready!')
    this.can.dom = document.getElementById('fishcanvas')
    this.can.context = this.can.dom.getContext('2d')
    this.fish.dom = document.getElementById('fish')
    this.fish.context = this.fish.dom.getContext('2d')
    this.fish.width = 100
    this.fish.height = 50
    this.can.rate = this.getCanvasRate(this.can.context)
    this.can.width = 750
    this.can.height = 750
    // this.can.width = screen.width
    // this.can.height = screen.height
    this.bginit() // 初始化背景
    this.fish.init() // 初始化鱼
    this.bgChange(0.1) // 让背景位置变动。
    var that = this
    that.fish.do('walk', true) // 做动作。
    setInterval(function () {
      that.fish.do('walk', true) // 换速度做动作。
    }, 15000)
    this.render() // 开始动画。
    window.ss = this
    this.time = (new Date()).getTime()
    this.drawFps()
    this.timec = this.time
    this.rotate = 0
    this.rotatespeed = 0
    window.devicerotate = 1
    window.devicerotate2 = 1

    window.addEventListener("deviceorientation", function(event) {
      if (event.isTrusted){
        window.devicerotate2 = parseInt(event.alpha) + '--' + parseInt(event.beta) + '----' + parseInt(event.gamma)
        var obj = {
          a: event.alpha,
          b: event.beta,
          g: event.gamma
        }
        that.changedeviceinfo('dir',obj)
      } else {
        window.devicerotate2 = '你没有用移动设备哦'
      }
    }, true);
    window.addEventListener("devicemotion", function(event) {
      var a = event.accelerationIncludingGravity
      if (event.isTrusted){
        window.devicerotate = parseInt(a.x) + '--' + parseInt(a.y) + '--' + parseInt(a.z)
        var obj = {
          x: a.x,
          y: a.y,
          z: a.z
        }
        that.changedeviceinfo('motion',obj)
      } else {
        window.devicerotate = '你没有用移动设备哦'
      }
    }, true);
    this.ladd('come') // 记录人来了。
    // window.addEventListener('onbeforeunload')
    window.onbeforeunload = function () {
      that.ladd('leave')
    }
    this.lcheck() // 检查人的记录

    this.ontap()
  },
  methods: {
    ontap: function () {
      var that = this
      var num = 0
      var words = ['不要摸我，流氓！', '你摸不到我，我们隔了一个屏幕', '点屏幕干嘛，来看我就好', '再点我也不会说我喜欢你']
      window.addEventListener('touchstart', function () {
        num++
        if (num % 10 == 9) {
          var ran = parseInt(Math.random() * 10) % 4
          that.say(words[ran]) // 做动作。
        }
      })
    },
    lset: function (key, obj) {
      var value = JSON.stringify(obj)
      window.localStorage.setItem(key, value)
    },
    lget: function (key) {
      var str = window.localStorage.getItem(key)
      var obj = []
      if (str) {
        obj = JSON.parse(str)
      }
      return obj
    },
    ladd: function (type) {
      var savekey = 'DZKX'
      var record = this.lget(savekey)
      var once = {
        time: (new Date()).getTime(),
        type: type
      }
      record.push(once)
      this.lset(savekey, record)
    },
    lcheck:function () {
      var that = this
      var savekey = 'DZKX'
      var record = this.lget(savekey)
      var times = 0
      for (var i = 0; i < record.length; i++) {
        if (record[i].type == 'come') {
          times++
        }
      }
      if (times > 5 && times < 10) {
        this.fish.do('talk', true, '你居然又来看我啦，你一定是喜欢我') // 做动作。
      } else if (times > 13 && times < 20) {
        this.fish.do('talk', true, '他们说我只有7s的记忆，可是我觉得我记得你~') // 做动作。
      } else if (times > 30) {
        if (record[record.length - 1].time - record[0].time > 7 * 24 * 60 * 1000) {
          this.fish.do('talk', true, '是的，你似乎常来看我，我喜欢你！') // 做动作。
          setTimeout(function () {
            that.fish.do('talk', true, '可惜我却不能转告给小凤！因为我没联网！坑！') // 做动作。
          }, 10000)
        } else {
          this.fish.do('talk', true, '嗯？天热，好烦躁啊，你改天再来看我吧') // 做动作。
        }
      } else {
        this.fish.do('talk', true, '请用心看-- 不要说话~ ~ ~') // 做动作。
      }
      setTimeout(function(){
        that.fish.do('talk', false)
      }, 5000)
    },
    say: function (str) {
      var that = this
      that.fish.do('talk', true, str) // 做动作。
      setTimeout(function(){
        that.fish.do('talk', false)
      }, 8000)
    },
    // 用户点击按钮。想要说个秘密的那个按钮。
    isay: function () {
      var that = this
      that.fish.do('talk', true, '我也不知道诶...') // 做动作。
      setTimeout(function(){
        that.fish.do('talk', false)
      }, 5000)
    },
    // 改变设备当前值. 同时修改背景的移动速度
    // 循环次数相当多！
    changedeviceinfo: function (name, val) {
      switch (name) {
        case 'motion':
        var lr = val.x // 左右倾斜设备。
        if (Math.abs(this.deviceinfo.leftOrRight - lr) > 0.3) { // 幅度 ： 0.3
          this.deviceinfo.leftOrRight = lr
          this.bgChange(0.5 * lr)
        }
        break;
        case 'dir':
        break;
        default:
        break;
      }
      this._computedUser(name, val)
    },
    // 根据数据计算当前用户手持设备的状态。
    // 循环次数相当多！
    _computedUser: function (name, val) {
      if (name === 'motion'){
        if (parseInt (val.x) == 0 && parseInt(val.y) == 0 && parseInt(val.z) <= -9) {
          if (this.deviceinfo.useraction != 'placePhoneHorizontal'){ // 如果之前不是拿着手机的状态
            this.deviceinfo.useraction = 'placePhoneHorizontal'
            this.fish.do('walk', true, {x:0.05, y:0.05, z: 0.05}) // 强行让鱼慢下来一下
          } else {
            // do nothing
          }
        } else {
          if (this.deviceinfo.useraction != 'holdingPhone'){
            this.fish.do('walk', true)
            this.deviceinfo.useraction = 'holdingPhone'
          }
          // this.deviceinfo.useraction = parseInt(val.x)
        }
      }
    },
    drawFps: function () {
      var timenow = (new Date()).getTime()
      var fps = 1000 / (timenow - this.time)
      // console.log('fps', fps)
      this.fps.push(fps)
      if(this.fps.length > 50) {
        this.fps.shift()
      }
      this.time = timenow

      if (timenow - this.timec > 1000) {
        this.fpsshow = this.fps[0] || 0
        this.timec = timenow
      }
      this.can.context.font = '36px serif';
      this.can.context.fillStyle = 'white';
      this.can.context.fillText(this.fpsshow, 50, 100);
    },
    drawText: function () {
      this.can.context.font = '16px serif';
      this.can.context.fillStyle = 'white';
      var str = window.devicerotate + '-==--' + window.devicerotate2 + '---' + this.deviceinfo.useraction ;
      // this.can.context.fillText(navigator.userAgent, 50, 100);
      str += '\n'
      str += this.bg.bg1.offleft
      str = this.deviceinfo.useraction ;
      var ua = navigator.userAgent;
      str = '如果你们经常来看一看我的鱼，它会很开心的，我也会很开心的'
      // if (ua.match(/iphone/gi)) {
      //   str += '手持 iphone 的'
      // } else if (ua.match(/Android/gi)) {
      //   str += '手持 android 的'
      // }
      // if (ua.match(/MicroMessenger/gi)) {
      //   str += '微信'
      // }
      // str += '朋友'
      this.can.context.fillText(str, 20, 50);
    },
    render: function () {
      // this.can.context.clearRect(0,0, 1000, 1000)
      // console.log('redner')
      var bg = this.bg.bg1
      this.bgChange()

      bg.left += bg.offleft // 背景左右偏移
      bg.top += bg.offtop // 背景上下偏移
      // bg.left -= bg.offleft // 背景左右偏移
      // bg.top -= bg.offtop // 背景上下偏移
      // console.log(bg.left, bg.offleft)
      // this.can.context.drawImage(bg.dom,-200,-200)
      // this.can.context.drawImage(bg.dom, 0,0, 1024*2, 576*2,bg.left,0, 750, 1334)
      // this.can.context.drawImage(bg.dom, 0,0, this.can.width*2, this.can.height*2,bg.left,bg.top,bg.width,bg.height)
      // this.can.context.drawImage(bg.dom,0,0,bg.width * bg.height/this.can.height,bg.height)
      // this.can.context.drawImage(bg.dom,0,0,bg.width * this.can.height/bg.height * this.can.rate,this.can.height* this.can.rate, bg.left,bg.top,bg.width,bg.height)
      this.can.context.drawImage(bg.dom, bg.left, bg.top, this.can.width * this.can.rate, this.can.height * this.can.rate, 0,0, this.can.width * this.can.rate, this.can.height * this.can.rate)
      

      var bgcxt = this.can.context
      var ff = this.fish
            // bgcxt.save();
            // bgcxt.translate(200, 200);//将新的坐标原点设置为(0,图片高度) 
            // if (this.rotatespeed > 99) {
            //   this.rotatespeed = (this.rotatespeed - 0.1) % 100
            // } else if (this.rotatespeed < 1) {
            //   this.rotatespeed = (this.rotatespeed + 0.1) % 100
            // } else {
              // this.rotatespeed = (this.rotatespeed + 0.1) % 100
            // }
            this.rotate = (this.rotate+0.1)%360
            // this.rotate = 30
            // console.log(this.rotate)
            // 左上rotate:10, headtoword: -1
            // 左下 rotate:310, headtword: -1
            // rightdown: rotate:20 ,headtword: 1
            //  righ up: rotate:330 , headtoword: 1 

            // console.log(this.rotate)
            // bgcxt.rotate(this.rotate * Math.PI/180);//垂直向上翻转       
      // this.can.context.translate(0,0)
      bgcxt.drawImage(ff.dom, ff.positon.x,  ff.positon.y, 150,50) // 鱼在画布上面的位置渲染
      // bgcxt.drawImage(ff.dom, -1 * ff.positon.x,  -1 * ff.positon.y, 150,50) // 鱼的位置渲染
      // bgcxt.drawImage(ff.dom, -50, -50, 150,50) // 鱼的位置渲染. 调试：不动的鱼
            // bgcxt.setTransform(1, 1, 1, 1, 1, 1);
            // bgcxt.restore();//恢复原点和翻转 

      this.fish.paint() // 鱼可能的外貌渲染
      this.fish.fishloop() // 鱼可能的动作渲染

      // this.drawFps() // 查看当前动画的帧率。
      this.drawText()

      requestAnimationFrame(this.render)
    },
    bginit: function () {
      var that = this
      var imgDom = document.createElement('img')
      imgDom.setAttribute('crossOrigin', 'anonymous')
      // imgDom.setAttribute('width', that.can.width)
      // imgDom.setAttribute('height', that.can.height)
      imgDom.setAttribute('src', '/mxf_kaistart/dist/static/fishbg3.jpeg')
      that.bg.bg1.dom = imgDom
      imgDom.onload = function () {
        that.bg.bg1.width = this.width
        that.bg.bg1.height = this.height
        // console.log(this.width, this.height)
        // that.can.context.drawImage(imgDom,0,0, 1000, 1000, 0,0,1000,1000)
      }
    },
    bgChange(offleft, offtop) {
      var bg = this.bg.bg1
      if (offleft !== undefined) {
        bg.offleft = offleft
      } else {
        // // 没有到达右边界的时候，画面一直向右
        // if (this.can.width*2 - bg.left > bg.width) { // 如果到达了右边界，立马向左偏移。
        //   bg.offleft = (-1) * bg.offleft
        // } else if (bg.left > 0 && bg.offleft > 0) { // 到达左边界之后，立即向右
        //   bg.offleft = (-1) * bg.offleft
        // } else{
        //   // 持续偏移
        // }
        // 没有到达右边界的时候，画面一直向右
        if (this.can.width + bg.left > bg.width && bg.offleft > 0) { // 如果到达了右边界，立马向左偏移。
          bg.offleft = (-1) * bg.offleft
          // bg.offleft = 0
        } else if (bg.left < 150 && bg.offleft < 0) { // 到达左边界之后，立即向右
          bg.offleft = (-1) * bg.offleft
        }else{
          // 持续偏移
        }
        // 加速度衰减
        if (Math.abs(bg.offleft) > 0.1) {
          if (bg.offleft < 0){
            bg.offleft = bg.offleft + 0.0015
          } else {
            bg.offleft = bg.offleft - 0.0015
          }
        }
      }
    },
    changecolor:function () {
      var ctx = this.fish.context
      var myImage = ctx.getImageData(0, 0, 100, 50);
      var picLength = this.fish.width * this.fish.height
        // Loop through data.
        for (var i = 0; i < picLength * 4; i += 4) {
          // First bytes are red bytes.        
          // Get red value.
          var myRed = myImage.data[i];
          // Second bytes are green bytes.
          // Get green value.
          var myGreen = myImage.data[i + 1];
          // Third bytes are blue bytes.
          // Get blue value.
          var myBlue = myImage.data[i + 2];
          // Fourth bytes are alpha bytes
          // We don't care about alpha here.
          // Add the three values and divide by three.
          // Make it an integer.
          var myGray = parseInt((myRed + myGreen + myBlue) / 3);
          if (myGray < 100) {
            myGray = myGray + 150
          }          
          // Assign average to red, green, and blue.
          if (i > picLength*3) {
            myImage.data[i] = myGray + 55;
            myImage.data[i + 1] = myGreen;
            myImage.data[i + 2] = myGray + 55;
          } else {
            myImage.data[i] = myGray + 185;
            myImage.data[i + 1] = myGreen;
            myImage.data[i + 2] = myGray;
          }
        }
        ctx.putImageData(myImage, 0, 0);
    },
    getCanvasRate: function (context) {
      var backingStore = context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio || 1
      return (window.devicePixelRatio || 1) / backingStore
    }
  }
}
</script>
<style>
*{
  margin: 0;
}
html,body{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.fishwrap{
  width: 750px;
  height: 750px;
}
.fishwrap{
  width: 100%;
  height: 100%;
}
